<template>
  <div class="title-wrapper">
    <side-menu/>
    <div class="title">{{modules}}</div>
  </div>
  <router-link :to='(levels[i-1].type == "quiz") ? "/quiz" : "/theory"'
      v-for='i in levels.length' :key='i'
      :class='levelIsOpened(i-1) ? "level-block" : "level-block disabled"'
      @click='setCurrentLevelIndex(levels[i-1].index)'>
    <!--<span class="material-icons level-icon">
      {{!levelIsOpened(i-1) ? 'lock' : (levels[i-1].type == 'quiz') ? 'widgets' : 'visibility'}}
    </span>-->
    <div class="progress-bar-wrapper" :style ='!levels[i-1].score 
      ? `background: lightgray` : (levels[i-1].type == "theory") 
      ? `background: #5F9EA0` : `animation: col 1s -${levels[i-1].score / levels[i-1].maxscore}s linear forwards paused`'>
        <progress-bar :ratio='levels[i-1].score ? (levels[i-1].score / levels[i-1].maxscore) : 0' col1='white' col2='white' :isTicked='(levels[i-1].type == "quiz")'></progress-bar>
    </div>
    <span class="level-block-body">
      <div class="level-name">
        {{(levels[i-1].type == "theory") ? 'Конспект: ' : ''}} {{levels[i-1].name}}
      </div>
      <div class="level-info">
        <span class="level-score" :style='levels[i-1].score ? "color: gray" : "color: lightgray"'>
          {{!levels[i-1].score ? 'Не розпочато' : (levels[i-1].type == "quiz") 
          ? `${levels[i-1].score} / ${levels[i-1].maxscore} балів` : 'Переглянуто' }}
        </span>
        <span class="level-stats">
          <span class="material-icons level-stats-icon">people</span>
          <span class="level-score">{{levels[i-1].total ?? 0}}</span>
        </span>
        <span class="level-stats">
          <span class="material-icons level-stats-icon">data_saver_off</span>
          <span class="level-score">{{Math.round((levels[i-1].avg ?? 0)*100)}}%</span>
        </span>
      </div>
    </span>
    <!--<div class="progress-bar-wrapper" :style ='!levels[i-1].score 
      ? `background: lightgray` : (levels[i-1].type == "theory") 
      ? `background: #5F9EA0` : `animation: col 1s -${levels[i-1].score / levels[i-1].maxscore}s linear forwards paused`'>
        <progress-bar :ratio='levels[i-1].score ? (levels[i-1].score / levels[i-1].maxscore) : 0' col1='white' col2='white' :isTicked='(levels[i-1].type == "quiz")'></progress-bar>
    </div>-->
  </router-link>
</template>

<script>
import sideMenu from '@/components/side-menu.vue'
export default {
  components: { sideMenu },
  computed: {
    levels() { return this.$store.state.levels },
    modules() { return this.$store.state.modules[localStorage.currentModuleIndex] },
  },
  methods: {
    setCurrentLevelIndex(index) { localStorage.setItem("currentLevelIndex", index) },
    levelIsOpened(index) { return index ? !!this.levels[index-1].score : true }
  }
}
</script>

<style lang="scss" scoped>
.title-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 20px; 
}
.level-block {
  width: 100%;
  height: 6em;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 5px;
  background: white;
  border-radius: 8px;
  border: 1px solid rgba(0,0,0,.1);
}
.disabled {
  pointer-events: none;
  opacity: .5;
}
.level-icon {
  font-size: 3em !important;
  font-weight: 100 !important;
  color: #323232;
  margin: 0 .4em;
}
.level-block-body {
  height: 6em;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-evenly;
  z-index: 1;
  padding-left: 1.4em;
  box-shadow: -8px 0 8px -6px rgba(0,0,0,.6);
 // @media (min-width: 500px) { box-shadow: 8px 0 8px -6px rgba(0,0,0,.6); }
}
.level-name {
  font-weight: bold; 
  font-size: 1.1em;
}
.level-info {
  display: flex;
  .level-score {
    color: gray;
    flex-grow: 1;
    margin-right: 15px;
  }
  .level-stats-icon {
    font-size: 1.25em !important;
    vertical-align: text-bottom;
    color: gray;
    margin-right: 5px;
  }
}
.progress-bar-wrapper {
  height: 6em;
  width: 6em;
  border-radius: 8px 0px 0px 8px;
  //box-shadow: 8px 0 8px -6px rgba(0,0,0,.6);
  background: white;
  //@media (max-width: 499px) { display: none }
  div {
    width: 4.5em;
    height: 4.5em;
    margin: 0.75em;
  }
}
</style>